import React from "react";
import { FlyoutWrapper, FlyoutTitle, FlyoutContent } from "./styled";

export type FlyoutProps = React.PropsWithChildren<{
  open: boolean;
  handleClose(): any;
  title?: string;
}>;

export const Flyout = (props: FlyoutProps) => {
  return (
    <FlyoutWrapper open={props.open} handleClose={props.handleClose}>
      <FlyoutContent
        transition={{ ease: [0.32, 0.4, 0.36, 1] }}
        initial={{ y: 100, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        exit={{ y: 100, opacity: -0.5 }}
      >
        {props.title && <FlyoutTitle>{props.title}</FlyoutTitle>}
        {props.children}
      </FlyoutContent>
    </FlyoutWrapper>
  );
};
